import React, { Component } from 'react';
import './formikStyle.css';
import { Route , withRouter, Link} from 'react-router-dom';

import { Formik } from 'formik';
import * as Yup from 'yup';


class FormikComponent extends Component {

    constructor(props){
        super(props);

        this.state = {
            firstName: '',
              lastName: '',
              email: '',

              username:'',
              todoId:''
        }
    }

    componentDidMount(){

        const {username,todoId} = this.props.parameterSent;

        
        console.log('username from props:',username)
        console.log('todoId from props:',todoId)

        this.setState({
            username:username,
            todoId:todoId
        })
    }

    cancelReturnListTodo = () =>{

        const username = this.state.username;
        
        this.props.history.push(`/todos/${username}`);
    }

    render(){

        console.log('inside Formik Component:',this.state.username)

        return(
            <div className='formik-form-main'>
                <Formik
                        initialValues= {{
                        firstName: this.state.firstName,
                        lastName: this.state.lastName,
                        email: this.state.email,
                        }}
                    validationSchema={ Yup.object({
                        firstName: Yup.string()
                        .max(15, 'Must be 15 characters or less')
                        .required('Required'),
                        lastName: Yup.string()
                        .max(20, 'Must be 20 characters or less')
                        .required('Required'),
                        email: Yup.string()
                        .email('Invalid email address')
                        .required('Required'),
                    })}
              onSubmit={(values, { setSubmitting }) => {
                setTimeout(() => {
                
                console.log('Formik form submitted with values=',values);

                setSubmitting(false);
                }, 400);
            }}
          >

            {formik => (
            <form onSubmit={formik.handleSubmit} className='formik-form-container'>
                
                <div className='formik-form-row'>
                    <input
                        id="firstName"
                        name="firstName"
                        type="text"
                        className='formik-form-input-text'
                        {...formik.getFieldProps('firstName')}
                        placeholder='First Name'
                    />
                    {formik.touched.firstName && formik.errors.firstName ? (
                        <div>{formik.errors.firstName}</div>
                    ) : null}
                </div>
                
                <div className='formik-form-row'>
                    <input
                        id="lastName"
                        name="lastName"
                        type="text"
                        className='formik-form-input-text'
                        {...formik.getFieldProps('lastName')}
                        placeholder='Last Name'
                    />
                    {formik.touched.lastName && formik.errors.lastName ? (
                            <div>{formik.errors.lastName}</div>
                        ) : null}
                </div>
                
                <div className='formik-form-row'>
                    <input
                        id="email"
                        name="email"
                        type="email"
                        className='formik-form-input-text'
                        {...formik.getFieldProps('email')}
                        placeholder='Email'
                    />
                    {formik.touched.email && formik.errors.email ? (
                        <div>{formik.errors.email}</div>
                    ) : null}
                </div>
                
                <div></div>
                <div>
                <Link className='router-link' to='/todos/user/'>To-dos</Link> 
                <button type='button' className='formik-form-submit-btn' onClick={this.cancelReturnListTodo}>Cancel</button>
                    <span>   </span>
                    <button type="submit" className='formik-form-submit-btn'>Submit</button>
                </div>
                
            </form>
            )}
            </Formik>
            </div>
        );
    }
}


export default FormikComponent;